import iHeader from "@/components/header/header-r";
import mycenterWeb from "@/components/webview/mycenterweb"

export default {
  name: "credit_circle",
  data() {
    return {
    users: localStorage.getItem('users') ? JSON.parse(localStorage.getItem('users')) : '',
    title: "我的邀请码",
    back:true,
    sharelist:[
            {
              icon:'#icon-weixin',
              name:'微信',
               type:'weixin'
            },{
                icon:'#icon-pengyouquan',
                name:'朋友圈',
                type:'weixinFriend'
            },{
                icon:'#icon-qq',
                name:'QQ',
                 type:'qq'
            },{
                icon:'#icon-xinlangweibo',
                name:'新浪微博' ,
                type:'sinaweibo'
            },{
                icon:'#icon-tengxunweibo-copy',
                name:'腾讯微博' ,
                type:'tencentweibo'
            }],
    //   share:true,
        shares:{},
        s:[],
    //    share: this.$route.query.share || false,
    };
  },
  components: {
    iHeader,mycenterWeb
  }, 
 mounted(){
        mui.plusReady(()=>{
            this.updateSerivces();
        });
   },
   methods:{    
       shareHref(item){//分享到那个地方去3
            switch (item) {
                case 'weixin':/*微信*/
                        this.shareMessage(this.shares['weixin'],'WXSceneSession');  
                    break;
                case 'weixinFriend':/*微信朋友圈*/
                        this.shareMessage(this.shares['weixin'],'WXSceneTimeline');  
                    break;
                case 'qq':
                        this.shareMessage(this.shares['qq']);
                    break;
                case 'tencentweibo':
                        this.shareMessage(this.shares['tencentweibo']);
                    break;
                default:
                        this.shareMessage(this.shares['sinaweibo']);
                    break;
            } 
        },
       updateSerivces() {//更新分享服务
            plus.share.getServices((s)=>{
                console.log(JSON.stringify(s),JSON.stringify(this.shares));
                this.s=s;
                for (var i in s) {
                    var t = s[i];//一个{}
                    this.shares[t.id] = t;//这个shares下面的id属性wexin对应这个json
                }
                this.outSet("获取分享服务列表成功");
            }, function(e) {
                this.outSet("获取分享服务列表失败：" + e.message);
            });
        },
    
        shareHrefs() {//顶部的分享1分享按钮点击事件
            var _this=this;
            var ids = [{
                    id: "weixin", 
                    ex: "WXSceneSession"  /*微信好友*/
                }, {
                    id: "weixin",
                    ex: "WXSceneTimeline" /*微信朋友圈*/
                }, {
                    id: "qq"   /*QQ好友*/
                },{
                    id: "sinaweibo"  /*新浪微博*/
                }, {
                    id: "tencentweibo"   /*腾讯微博*/
                }],
                bts = [{
                    title: "发送给微信好友"
                }, {
                    title: "分享到微信朋友圈"
                }, {
                    title: "分享到QQ"
                }, {
                    title: "分享到新浪微博"
                }, {
                    title: "分享到腾讯微博"
                }];
            plus.nativeUI.actionSheet({
                    cancel: "取消",
                    buttons: bts
                },
                function(e) {
                    var i = e.index;
                    if (i > 0) {
                        _this.shareAction(ids[i-1].id, ids[i - 1].ex);
                    }
                },
            );
        }, 
        shareAction(s, ex) {//用户分享信息前需要对分享平台是否授权过进行判断，如果已经授权则可直接分享消息，如没有授权则需要调用已保存的分享对象的“authorize”接口对客户端进行授权。授权成功后再调用分享接口分享消息。
                this.outSet("分享操作：",s);
                if (!s) {
                    this.outLine("无效的分享服务！");
                    return;
                }
                if (s.authenticated) {
                    this.outLine("---已授权---");
                    this.shareMessage(s, ex);
                } else {
                    this.outLine("---未授权---");
                    s.authorize(function() {
                        this.shareMessage(s, ex);
                    }, function(e) {
                        this.outLine("认证授权失败：" + e.code + " - " + e.message);
                    });
                }
        },
        // shareAction(id, ex) {//2 分享操作
        //    var s = this.shares[id];
        //     if (!id ) {
        //         this.outLine("无效的分享服务！");
        //         return;
        //     }
        //     console.log(id,JSON.stringify(s))
        //     if (s.authenticated) {
        //         this.outSet("---已授权---");
        //          this.shareMessage(id, ex);
        //     } else {
        //         this.outSet("---未授权---");
        //         s.authorize(function() {
        //             this.shareMessage(id, ex);
        //         }, function(e) {
        //             this.outLine("认证授权失败");
        //         });
        //     }
        // },      
        shareMessage(s, ex) {//4发送分享消息 
            console.log('444',JSON.stringify(s))
             var msg = {
                content: '分享-详情',
                href: 'http://www.kbbabc.com/api/my_invite/show_register?invite_code='+this.users.mobile,
                title: '金灿灿开始赚钱了,不来就OUT了',
                content: '推荐使用金灿灿app,下载金灿灿让你有花不完的钱',
                thumbs: ['http://jcc.tw186.com/uploads/img/index.png'],
                pictures: ['http://jcc.tw186.com/uploads/img/index.png'],
                extra: {
                    scene: ex
                }
            };
              s.send( msg, function(){
                    mui.toast( "分享到\""+s.description+"\"成功！ " );
                }, function(e){
                    mui.toast( "分享到\""+s.description+"\"失败: "+e.code+" - "+e.message );
                } );
        },
        outSet(msg) {
            console.log(msg);
        },
        outLine(msg) {
            mui.toast(msg);
        },
   }
};
   



